.navbar {
  width: 100%;
  max-width: 100%;
  height: 70px;
  display: flex;
  -webkit-transition: 0.3s all;
  transition: 0.3s all;
  position: fixed;
  left: 0;
  padding: 5px;
  top: 0;
  justify-content: space-between;
  z-index: 999; }
  @media screen and (max-width: 375px) {
    .navbar {
      justify-content: center; }
      .navbar .navbar-logo {
        display: none; }
      .navbar .navbar-links {
        font-size: 13px; }
      .navbar .navbar-left {
        padding-left: 0px !important; }
        .navbar .navbar-left .navbar-links .navbar-link {
          margin-right: 3px !important; }
      .navbar .navbar-right {
        padding-right: 0px !important; }
        .navbar .navbar-right .navbar-social-media .navbar-link {
          margin-right: 3px !important; } }
  .navbar .navbar-left {
    display: flex;
    align-items: center;
    padding-left: 5px; }
    .navbar .navbar-left .navbar-logo {
      width: 40px;
      margin-right: 15px; }
      .navbar .navbar-left .navbar-logo img {
        width: 100%; }
    .navbar .navbar-left .navbar-links {
      display: flex;
      -webkit-margin-before: 0;
              margin-block-start: 0;
      -webkit-margin-after: 0;
              margin-block-end: 0;
      list-style-type: none;
      -webkit-margin-start: 0px;
              margin-inline-start: 0px;
      -webkit-margin-end: 0px;
              margin-inline-end: 0px;
      -webkit-padding-start: 0;
              padding-inline-start: 0; }
      .navbar .navbar-left .navbar-links .navbar-link {
        color: #ffff66;
        background: #392270;
        min-width: 50px;
        border-radius: 25px;
        padding: 10px;
        text-decoration: none;
        -webkit-transition: 0.3s all;
        transition: 0.3s all;
        margin-right: 10px; }
        .navbar .navbar-left .navbar-links .navbar-link:hover, .navbar .navbar-left .navbar-links .navbar-link.active {
          background: #ffff66;
          padding: 10px;
          border-radius: 25px;
          color: #01082d; }
  .navbar .navbar-right {
    display: flex;
    padding-right: 25px;
    align-items: center; }
    .navbar .navbar-right .navbar-social-media {
      -webkit-margin-before: 0;
              margin-block-start: 0;
      -webkit-margin-after: 0;
              margin-block-end: 0;
      list-style-type: none;
      -webkit-margin-start: 0px;
              margin-inline-start: 0px;
      -webkit-margin-end: 0px;
              margin-inline-end: 0px;
      -webkit-padding-start: 0;
              padding-inline-start: 0;
      display: flex; }
      .navbar .navbar-right .navbar-social-media .navbar-link {
        color: #392270;
        background: #392270;
        text-decoration: none;
        font-size: 30px;
        -webkit-transition: 0.3s all;
        transition: 0.3s all;
        width: 40px;
        display: inline-flex;
        align-content: center;
        height: 40px;
        position: relative;
        border-radius: 50%;
        margin-right: 10px; }
        .navbar .navbar-right .navbar-social-media .navbar-link:hover {
          color: #ffff66;
          background: #ffff66; }
          .navbar .navbar-right .navbar-social-media .navbar-link:hover i {
            background: #392270; }
        .navbar .navbar-right .navbar-social-media .navbar-link i {
          background: #ffff66;
          width: 24px;
          top: 9px;
          left: 8px;
          height: 20px;
          position: absolute; }
          .navbar .navbar-right .navbar-social-media .navbar-link i::before {
            position: absolute;
            margin-top: -5px;
            margin-left: -1px; }

